<template>
<div>
  <div class="home_top">
      <ul>
          <li v-for="(nav,key) in navs" @click="changeNavs($event)" :class="{ cur: key === 0 }">{{nav}}</li>
      </ul>
  </div>
  <div class="home">
        <div class="kuang">
        <div class="newman">
            <div class="pic_area">
                <div class="pic_ba">
                    <div class="swiper-container swiper-container-horizontal">
                        <div class="swiper-wrapper">
                            <div class="swiper-slide" v-for="item in banner">
                                <div class="pic_swiper" v-bind:style="'background-image:url(' + item + ')'"><router-link to="/shop"></router-link></div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- 新人专场 -->
                <div class="pic_ex" v-for="one in newPeople">
                    <div class="pic_tp" v-bind:style="'background-image:url(' + one.pic + ')'"><router-link to="/shop"></router-link></div>
                    <div class="pic_ad">
                        <div class="xsjt"></div>
                        <div class="xrzc">
                            <div class="xrzcz">【{{one.name}}】</div>
                            <div class="ksch">{{one.title}}</div>
                        </div>
                        <div class="daz">
                            <span class="zheqi">折起</span>
                            <span class="jiz">{{one.sale}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="gap"></div>
            <div class="newpin">
                <div class="jrsx">
                    <div class="xptp"></div>
                    <div class="jrsxz">今日上新</div>
                </div>
                <div class="shop_show">
                <!-- 店铺上新 -->
                  <div class="pic_ex" v-for="good in newIn">
                      <div class="pic_tp" v-bind:style="'background-image:url(' + good.pic + ')'"><router-link to="/shop"></router-link></div>
                      <div class="pic_ad">
                          <div class="xsjt"></div>
                          <div class="xrzc">
                             <div class="xrzcz">{{good.name}}</div>
                             <div class="ksch">{{good.title}}</div>
                          </div>
                          <div class="daz">
                            <span class="zheqi">折起</span>
                            <span class="jiz">{{good.sale}}</span>
                          </div>
                      </div>
                   </div>
                </div>
            </div>
        </div>
      </div>
      <div class="seat"></div>
    </div>
    <myfooter></myfooter>
  </div>
</template>
<script>
  // 导航
  var navsMsg = ['今日热点','女士','男士','美妆','家居','美食','运动','童装'];
  // banner
  var bannerMsg = ['http://www.easyinto.cn/image/161230180538_7601.png', 'http://www.easyinto.cn/image/161230180538_3390.png', 'http://www.easyinto.cn/image/161230180538_7809.png'];
  // 新人专场
  var newPeopleMsg = [{pic:'http://www.easyinto.cn/image/161230180538_3543.png', name:'新人专场',title:'开启吃货人生',sale:'3'},];
  // 今日上新
  var newInMsg = [{pic:'http://www.easyinto.cn/image/161230180538_3390.png', name:'AGNONA',title:'女士服装配饰',sale:'3'},{pic:'http://www.easyinto.cn/image/161230180538_7809.png', name:'AGNONA',title:'女士服装配饰',sale:'3'},];
  import myfooter from 'components/footer' // 页脚
  // 输出组件
  export default {
    name: 'home',
    components: {
      myfooter
    },
    data () {
      return {
        navs: navsMsg,
        banner: bannerMsg,
        newPeople: newPeopleMsg,
        newIn: newInMsg,
      }
    },
    // 钩子函数
    mounted () {
      // banner 长度
      $('.home_top>ul').css('width', navsMsg.length * 1.3 + 'rem');
      // banner swiper
     var swiper = new Swiper('.swiper-container', {
      // pagination: '.swiper-pagination',
      // paginationClickable: true,
      // loop: true
    });
   },
   methods: {
      changeNavs: function($event){
        $('.home_top>ul>li').removeClass('cur');
        $($event.currentTarget).addClass('cur');
      }
   },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  html, body, p, h1, h2, h3, h4, h5, h6, div, ul, dl, dd, dt, li, i, b, input,textarea {
    padding: 0;
    margin: 0;
    font-weight: normal;
    font-style: normal;
  }
  ul,li{
    list-style: none;
  }
  body{
    font-family: 'microsoft yahei';
    font-size: .16rem;
  }
  a {
    text-decoration: none;
    color:#222222;
  }
  /*首页样式*/
  .home_top{
    position: fixed;
    top:0;
    left:0;
    width:100%;
    height: 0.8rem;
    background-color: #fff;
    padding:0 0.2rem;
    box-sizing: border-box;
    z-index: 1000;
    overflow: auto
  }
  .home_top>ul{
    width: 10.5rem;
    height: 100%;
  }
  .home_top>ul>li{
    float: left;
    height: 0.8rem;
    width:1.3rem;
    color: #999;
    font-size: 0.24rem;
    text-align: center;
    line-height: 0.8rem;
    box-sizing: border-box;
    border-bottom: 0.04rem solid #fff;
  }
  .home_top>ul>li.cur{
    border-bottom:0.04rem solid #000;
    color:#000;
    font-weight: bold;
  }
  .home{
    margin-top: 0.8rem;
    margin-bottom: 0.85rem;
  }
  .newman{
    width:100%;
  }
  .newman .pic_ba{
    width:100%;
    height: 3.7rem;
    margin-bottom: 0.1rem;
  }
  .newman .pic_ba .pic_swiper{
    width:100%;
    height: 3.7rem;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
  }
  .newman .pic_ba .pic_swiper>a{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .pic_ex{
    width: 100%;
  }
  .pic_tp{
    width:100%;
    height: 3.4rem;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
  }
  .pic_tp>a{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .pic_ad{
    width:100%;
    height: 1.1rem;
    padding:0.2rem 0.2rem;
    box-sizing: border-box;
    position: relative;
  }
  .pic_ad .xrzc{
    float: left;
    width:60%;
  }
  .pic_ad .xrzc .xrzcz{
    width:50%;
    font-size: 0.26rem;
    color: #000;
    margin-bottom: 0.1rem;
  }
  .pic_ad .xrzc .ksch{
    font-size: 0.24rem;
    color: #777;
  }
  .pic_ad .daz{
    float: right;
    width:1.3rem;
    height: 100%;
    padding:0.08rem;
    box-sizing: border-box;
  }
  .xsjt{
    position: absolute;
    width:0.24rem;
    height: 0.24rem;
    transform: rotate(45deg);
    top:-0.12rem;
    left:0.2rem;
    background-color: #fff;
    z-index: 10;
  }
  .daz .jiz{
    float: right;
    font-size: 0.34rem;
    color: #ec0a29;
    margin-right: 0.1rem;
  }
  .daz .zheqi{
    float: right;
    line-height: .52rem;
    font-size: 0.24rem;
    color:#777;
  }
  .gap{
    width:100%;
    height: 0.3rem;
    background-color: #e6e8ec;
    box-shadow: inset 0 0 0.06rem rgba(0,0,0,0.2);
  }
  .newpin .jrsx{
    width: 100%;
    height: 1rem;
    padding:0.3rem 0.2rem;
    box-sizing: border-box;

  }
  .jrsx .xptp{
    width: 1.17rem;
    height: 0.25rem;
    background-image: url(../assets/home/1-0-new-in.png);
    background-size: cover;
    background-repeat: no-repeat;
  }
  .jrsx>div{
    float: left;
  }
  .jrsx .jrsxz{
    margin-left: 0.1rem;
    font-size: 0.24rem;
    color:#000;
  }
  .newpin .shop_show{
    width: 100%;
  }
  .kuang{
    width:100%;
    overflow: auto;
  }
</style>



