<template>
  <div class="trace">
    <div class="tpzs" v-for="trace in traces" v-bind:style="'background-image:url(' + trace.pic + ')'">
      <div class="zc">
        <div class="msz">
          <div class="mszd">{{trace.title}}</div>
          <div class="mszx">{{trace.flag}} / {{trace.time}}</div>
        </div>
      </div>
    </div>
   <myfooter></myfooter>
  </div>
</template>
<script>
  var tracesMsg = [{title:'这个冬季的色彩已经出库',flag:'#大牌',time:'12.13',pic:'http://www.easyinto.cn/image/161230180538_7601.png'},{title:'柏林式的仙履奇缘',flag:'#大牌',time:'12.13',pic:'http://www.easyinto.cn/image/161230180538_3543.png'},{title:'玩转昼夜的时髦配对装',flag:'#穿搭',time:'12.13',pic:'http://www.easyinto.cn/image/161230180538_3390.png'},{title:'日常的营养搭配',flag:'#营养',time:'12.13',pic:'http://www.easyinto.cn/image/161230180538_7809.png'},];
  import myfooter from 'components/footer' // 页脚
  export default {
    name: 'trace',
    components: {
      myfooter
    },
    data () {
      return {
        traces: tracesMsg,
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  html, body, p, h1, h2, h3, h4, h5, h6, div, ul, dl, dd, dt, li, i, b, input,textarea {
    padding: 0;
    margin: 0;
    font-weight: normal;
    font-style: normal;
  }
  ul,li{
    list-style: none;
  }
  body{
    font-family: 'microsoft yahei';
    font-size: .16rem;
  }
  a {
    text-decoration: none;
    color:#222222;
  }
  .trace{
    margin-bottom: 0.85rem;
  }
  .tpzs{
    width:100%;
    height: 4.1rem;
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .zc{
    width:100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;
    background-color: rgba(0,0,0,0.4);
    z-index: 8;
  }
  .msz{
    width:100%;
    height: 1rem;
    position: absolute;
    top:50%;
    left:0;
    margin-top: -0.5rem;
  }
  .mszd{
    font-size: 0.36rem;
    color: #fff;
    text-align: center;
    font-weight: bold;
    margin-bottom: 0.16rem;
    text-shadow: 0 0 1px rgba(0,0,0,0.2);
  }
  .mszx{
    font-size: 0.24rem;
    color: #fff;
      text-align: center;
      font-weight: bold;
      text-shadow: 0 0 1px rgba(0,0,0,0.2);
  }
  .jg{
    width:100%;
    height: 0.1rem;
    background-color: #fff;
  }
</style>



