// 媒体查询
import './libs/common.js'
import './libs/swiper.css'
// 引入插件
// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import $ from 'jquery'
import swiper from 'swiper'
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
// 引入模块
import App from './App'  // 页脚
import home from './components/home' // 首页
import classify from './components/classify' // 分类
import trace from './components/trace' // 追溯
import shoppingCart from './components/shoppingCart' // 购物车
import personalCenter from './components/personalCenter' // 个人中心
import shop from './components/shop' // 店铺首页
import detail from './components/detail' // 商品详情
Vue.use(VueRouter)
Vue.use(VueResource)
// 声明路由
const routes = [
    { path: '/', component: home , name: 'home'},
 	{ path: '/classify', component: classify,name:'classify'},
	{ path: '/trace', component: trace,name:'trace'},
	{ path: '/shoppingCart', component: shoppingCart,name:'shoppingCart'},
	{ path: '/personalCenter', component: personalCenter,name:'personalCenter'},
	{ path: '/shop', component: shop,name:'shop'},
	{ path: '/detail', component: detail,name:'detail'},
]
// 启动路由
const router = new VueRouter({
    routes: routes
})
/* eslint-disable no-new */
// 启动app
var app = new Vue({
  el: '#app',
  router,
  ...App,
});



