<template>
  <div class="shoppingCart">
    <div class="header">
      <p><i></i></p>
      <span>免运费</span>
    </div>
    <!-- 一个店铺的商品 -->
    <div class="shop">
      <!-- 店铺名称 -->
      <div class="shop-line"></div>
      <div class="shop-name">
        <p><i></i></p>
        <span>魅力惠直发</span>
      </div>
      <!-- 一个店铺商品 -->
      <div class="good" v-for="good in goods">
        <div class="choose"><i></i></div>
        <div class="details">
          <div class="pic" v-bind:style="'background-image:url(' + good.pic + ')'"><router-link to="/eat"></router-link></div>  <!--图片-->
          <div class="describe">
            <p class="des-text"><router-link to="/eat"></router-link>{{good.shop}}</p>
            <p class="des-text des-text-small"><router-link to="/eat"></router-link>{{good.name}}</p>
            <div class="des-size"><span>{{good.color}}</span><span>{{good.size}}</span></div>
            <div class="des-num">
              <div class="num-minus"><i></i></div>
              <div>{{good.num}}</div>
              <div class="num-plus"><i></i></div>
            </div>
          </div>
          <div class="price">
            <p class="pri-original">￥{{good.origprice}}</p>
            <p class="pri-discount">￥{{good.discprice}}</p>
            <i class="delete"></i>
          </div>
        </div>
      </div>
    </div>
    <!-- 结算信息 -->
    <div class="settlement">
      <span><i></i></span>
      <p>全选</p>
      <div class="all-pri">
        <p class="orig">总计：￥<span>716.00</span></p>
        <p class="disc">为您节省￥<span>1672.00</span></p>
      </div>
      <div class="set"><router-link to="/settlementCenter"></router-link>结算(<span>6</span>)</div>
    </div>
    <div class="alert-box" style="display:none;">
      <div class="confirm">
        <div class="alert-text"><div>确认删除该商品吗？</div></div>
        <div class="alert-btn">
          <div class="no-btn">取消</div>
          <div class="yes-btn">确定</div>
        </div>
      </div>
    </div>
    <!-- 占位符 -->
    <div class="seat"></div>
    <myfooter></myfooter>
  </div>
</template>

<script>
  var goodsMsg = [{shop:'The Kooples Sport',name:'红黑拼色圆领横条纹长袖针织衫',color:'红黑色',size:'XL',num:'3',origprice:'2388.00',discprice:'716.00',pic:'http://106.3.37.173:81/image/170103102217_7501.jpg'},{shop:'The Kooples Sport',name:'红黑拼色圆领横条纹长袖针织衫',color:'红黑色',size:'XL',num:'1',origprice:'2388.00',discprice:'716.00',pic:'http://106.3.37.173:81/image/170103102217_7501.jpg'},{shop:'The Kooples Sport',name:'红黑拼色圆领横条纹长袖针织衫',color:'红黑色',size:'XL',num:'2',origprice:'2388.00',discprice:'716.00',pic:'http://106.3.37.173:81/image/170103102217_7501.jpg'},];
  import myfooter from 'components/footer' // 页脚
  export default {
    name: 'shoppingCart',
    components: {
      myfooter
    },
    data () {
      return {
        goods: goodsMsg,
      }
    }
  }
  $(function(){
    // 选择单个
    $('body').on('click', '.choose', function(e) {
      e.stopPropagation();
      if ($(this).children('i').hasClass('cur')) {
        $('.shop-name>p>i').removeClass('cur');
        $('.settlement>span>i').removeClass('cur');
      }
      $(this).children('i').toggleClass('cur');
    });
    // 选择店铺
    $('body').on('click', '.shop-name>p', function(e) {
      e.stopPropagation();
      if ($(this).children('i').hasClass('cur')) {
        $(this).parents('.shop').find('.choose>i').removeClass('cur');
        $('.settlement>span>i').removeClass('cur');
      }else{
        $(this).parents('.shop').find('.choose>i').addClass('cur');
        $('.settlement>span>i').addClass('cur');
      }
      $(this).children('i').toggleClass('cur');
    });
    // 全选
    $('body').on('click','.settlement>span', function(e) {
      e.stopPropagation();
      if ($(this).children('i').hasClass('cur')) {
        $('.shop-name>p>i').removeClass('cur');
        $('.choose>i').removeClass('cur');
      }else{
        $('.shop-name>p>i').addClass('cur');
        $('.choose>i').addClass('cur');
      }
      $(this).children('i').toggleClass('cur');
    });
    // 删除
    $('body').on('click','.good .delete',function(e){
      e.stopPropagation();
      $('.alert-box').show();
    })
    $('body').on('click','.no-btn',function(e){
      e.stopPropagation();
      $('.alert-box').hide();
    })
    $('body').on('click','.yes-btn',function(e){
      e.stopPropagation();
      $('.alert-box').hide();
    })
    // 数量减
    $('body').on('click', '.num-minus', function(e) {
      e.stopPropagation();
      var num = $(this).next('div').text();
      if (num > 1) {
        $(this).next('div').text(--num);
      }
    });
    // 数量加
    $('body').on('click', '.num-plus', function(e) {
      e.stopPropagation();
      var num = $(this).prev('div').text();
      // if (num>1) {
        $(this).prev('div').text(++num);
      // }
    });
  })
</script>

